<template>
  <div class="">
    <HeadTop :Headtype="Headtype" :title="$t('presale.预售')"></HeadTop>
    <div class="p15">
      <div class="sale-info flex-between">
        <div class="sale-info__item">
          <div class="number">{{usdtBalance}}</div>
          <div class="title">{{$t('presale.USDT可用余额')}}</div>
        </div>
        <div class="sale-info__item">
          <div class="number">{{canBuyAmount}}</div>
          <div class="title">{{$t('presale.ART剩余数量')}}</div>
        </div>
      </div>
      <div class="sale-time">
        <div class="sale-time__start border-1px-b">{{$t('presale.预售开始时间')}}：2021-07-31</div>
        <!-- <van-count-down :time="time" >
          <template #default="timeData">
            <span class="block">{{ timeData.days }}</span>
            <span class="colon">{{$t('presale.天')}}</span>
            <span class="block">{{ timeData.hours }}</span>
            <span class="colon">{{$t('presale.时')}}</span>
            <span class="block">{{ timeData.minutes }}</span>
            <span class="colon">{{$t('presale.分')}}</span>
            <span class="block">{{ timeData.seconds }}</span>
            <span class="colon">{{$t('presale.秒')}}</span>
          </template>
        </van-count-down> -->
      </div>
      <div class="sale-rechange">
        <van-cell-group inset>
          <div class="flex-between border-1px-b">
            <van-field v-model="number" :label="$t('presale.兑换')" input-align="right" :placeholder="$t('presale.请输入数量')" /> ART
          </div>
          <div class="flex-between">
            <van-field v-model="getnumber" class="getNum" :label="$t('presale.需要')" readonly input-align="right" placeholder="" /> USDT
          </div>
        </van-cell-group>
      </div>
      <div class="sale-text">{{$t('presale.最低金额')}}：{{price}}USDT，{{$t('presale.最高金额')}}：{{price * 10}}USDT，1ART={{price}}USDT</div>
      <div class="rech-btn">
        <van-button v-if="Number(usdtAllowance)" @click="buy" type="primary">{{$t('presale.兑换')}}</van-button>
        <van-button v-else @click="approveUsdt" type="primary">{{$t('presale.授权USDT')}}</van-button>
      </div>
      <div class="record">
        <div class="right" @click="$router.push({name: 'Prerecode'})">
          {{$t('presale.记录')}}<img class="icon" src="@/assets/images/arr-r@2x.png">
        </div>
      </div>
      <div class="sale-record">
        <div class="border-1px-b add-code">
          <div class="qrcode-box">
            <img :src="imgQrcode" alt="">
          </div>
        </div>
        <div class="address-tit flex-center">{{$t('presale.预售合约地址')}}</div>
        <div class="address-text flex-center">
          <div class="info">
            {{link}}
            <img src="@/assets/images/copy.png" class="copy clip-button" :data-clipboard-text="link">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import HeadTop from '@/components/HeadTop' 
import { reactive, toRefs, computed, watch } from "vue";
import QRCode from 'qrcode'
import { Toast } from 'vant';
import {useStore} from "vuex";
import Clipboard from 'clipboard'
import { useI18n } from "vue-i18n";
import config from "@/utils/config";
import ArtAbi from "@/utils/ArtAbi";
import tokenAbi from "@/utils/abi";
import { intro } from "@/storage";
import { MaxUint256, AddressZero } from "@ethersproject/constants"
import Bignumber from "bignumber.js"
export default {
  components: {
    HeadTop
  },
  setup() {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      $t: t,
      Headtype: '',
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      number: '',
      intro: intro.get() || AddressZero,
      getnumber: computed(() => {
        if (Number(data.price)&& data.number) {
          return Number(data.number * data.price).toFixed(4)
        }
        return '0.0000'
      }),
      artContract: null,
      artTokenContract: null,
      usdtToken: null,
      imgQrcode: '',
      link: '',
      usdtAddress: '',
      usdtBalance: '0.0000',
      usdtAllowance: '',
      canBuyAmount: '',
      price: '0',
      time: 30 * 60 * 60 * 1000,
      initContract: () => {
        data.artContract = new data.web3.eth.Contract(ArtAbi, config.ArtAddress)
        data.artTokenContract = new data.web3.eth.Contract(tokenAbi, config.TokenAddress)
        data.initData()
      },
      initData: () => {
        var date = new Date('2021-07-31 04:00');
        data.Unlocktime = date.getTime();
        data.newTime = parseInt(Date.now())
        data.time = data.Unlocktime - data.newTime
        data.link = `https://bscscan.com/address/${config.ArtAddress}`
        QRCode.toDataURL(data.link, {margin: 0.5, width: 168})
        .then(url => {
          data.imgQrcode = url
          console.log(data.imgQrcode)
        })
        .catch(err => {
          console.error(err)
        })
        var clipboard = new Clipboard('.clip-button')
        clipboard.on('success', function (e) {
          Toast(t('share.复制成功'))
          e.clearSelection()
        })
        clipboard.on('error', function () {
          Toast(t('share.复制失败，请手动长按复制'))
        }) 
        data.getUsdtAddress()
        data.getCanBuyAmount()
        data.getPrice()
      },
      getUsdtAddress: async () => {
        let address = await data.artContract.methods.usdtAddress().call()
        // let address = '0xff9f29e2dd5f3c76ec0c3214e5bb758e7f67f462'
        data.usdtToken = new data.web3.eth.Contract(tokenAbi, address)
        let balance = await data.usdtToken.methods.balanceOf(data.address).call()
        let decimals = await data.usdtToken.methods.decimals().call()
        data.usdtBalance = Number(balance/Math.pow(10, decimals)).toFixed(4)
        data.usdtAllowance = await data.usdtToken.methods.allowance(data.address, config.ArtAddress).call()
      },
      getCanBuyAmount: async  () => {
        let haveBuy = await data.artContract.methods.haveBuyAmount().call()
        let total = await data.artContract.methods.totalAmount().call()
        console.log(haveBuy, total)
        data.canBuyAmount = Number((total - haveBuy)/Math.pow(10, 18)).toFixed(4)
      },
      getPrice: async () => {
        let price = await data.artContract.methods.price().call()
        data.price = price
      },
      approveUsdt: async () => {
        await data.usdtToken.methods.approve(config.ArtAddress,MaxUint256).send({from: data.address})
        data.initData()
      },
      buy: async () => {
        if(Number(data.time) > 0) {
          Toast(t('presale.未到预售时间'))
          return
        }
        if(Number(data.number) < 1) {
          Toast(t('presale.数量必须大于1'))
          return
        }
        if(Number(data.usdtBalance) < Number(data.getnumber)) {
          Toast(t('presale.USDT数量不足'))
          return
        }
        let amount = new Bignumber(data.number).multipliedBy(Math.pow(10, 18)).toString()
        await data.artContract.methods.invest(amount, data.intro).send({from: data.address})
      }
    })
    if (data.address) {
      data.initContract()
    }
     watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less">
  .sale-time{
    .van-count-down{
      text-align: center;
      padding: 20px 0 28px;
    }
  }
  .sale-rechange{
    .van-cell-group{
      border-radius: 10px;
      padding: 0 12px;
      font-size: 15px;
      color: #111111;
      &::after{
        content: none;
      }
      .van-field{
        padding: 13px 10px 13px 0;
        font-size: 15px;
        background: transparent;
      }
      .getNum{
        .van-field__control{
          color: #0066EE;
          font-size: 18px;
        }
      }
    }
  }
</style>
<style lang="less" scoped>
  .sale-info{
    background: #64BCFF;
    border-radius: 10px;
    padding: 28px 0;
    .sale-info__item{
      text-align: center;
      width: 50%;
      color: #FFFFFF;
      .number{
        font-size: 18px;
        margin-bottom: 5px;
      }
      .title{
        font-size: 13px;
      }
    }
  }
  .record{
    .right{
      color: #fff;
      font-size: 16px;
      font-weight: 700;
      padding: 0 0 11.5px;
      display: flex;
      align-items: center;
      justify-content: flex-end;

    }
    .icon{
      margin-left: 9px;
      width: 6px;
      height: 11px;
      display: block;
    }
  }
  .sale-time{
    background: #FFFFFF;
    border-radius: 10px;
    padding: 0 15px;
    margin: 15px 0;
    .sale-time__start{
      padding: 12px 0;
      font-size: 15px;
      color: #333333;
    }
    .colon {
      display: inline-block;
      margin: 0 5px;
      color: #0066EE;
      font-weight: bold;
    }
    .block {
      display: inline-block;
      width: 22px;
      color: #fff;
      font-size: 15px;
      padding: 5px 0;
      text-align: center;
      background: #0066EE;
      border-radius: 2px;
    }
  }
  .sale-rechange{
    
  }
  .sale-text{
    font-size: 12px;
    color: #FFFFFF;
    margin-top: 10px;
  }
  .rech-btn{
    margin: 30px auto 15px;
    button{
      width: 100% !important;
      background: #0066EE;
    }
    .van-button--disabled{
      background: #B9C3CB;
      border: 0;
      opacity: 1;
    }
  }
  .sale-record{
    background: #FFFFFF;
    border-radius: 10px;
    padding: 40px 0;
    .add-code{
      padding-bottom: 35px;
    }
    .qrcode-box {
      width: 150px;
      height: 150px;
      padding: 4px;
      margin: 0 auto;
      img{
        width: 100%;
        height: 100%;
      }
    }
    .address-tit{
      margin: 25px 0 10px;
      font-size: 15px;
      color: #000000;
    }
    .address-text{
      background: rgba(185, 195, 203, .3);
      border-radius: 10px;
      padding: 17px 0;
      margin: 0 12px;
      .info{
        font-size: 14px;
        color: #333333;
        line-height: 25px;
        word-wrap:break-word;
        width: 72%;
        text-align: center;
        .copy{
          width: 14px;
          height: 14px;
          margin-left: 10px;
        }
      }
    }
  }
</style>
